<template>
  <v-container
    fluid
    style="margin: 0px !important; padding: 0px important!;"
    class="main__subappbar ma-0 pa-0"
  >
    <v-card
      color="grey lighten-4"
      flat
      tile
      style="margin: 0px; border-radius: 0px;"
    >
      <div class="row__subappbar">
        <div class="image_subappbar">
          <v-img
            class="logo__subappbar"
            src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/logo_web_bpdgpj"
            @click="returnToHome()"
          />
        </div>
        <div class="buttons__subappbar">
          <v-btn
            style="cursor: pointer"
            text
            color="#545454"
            eager
            plain
            to="/"
            @mouseover="hideMenu()"
          >
            Inicio
          </v-btn>

          <v-btn
            text
            plain
            color="#545454"
            @mouseover="showMenu(1)"
          >
            Especialidades
            <v-icon color="warning">
              mdi-menu-down
            </v-icon>
          </v-btn>

          <v-btn
            text
            plain
            color="#545454"
            @mouseover="showMenu(2)"
          >
            Servicios
            <v-icon color="warning">
              mdi-menu-down
            </v-icon>
          </v-btn>

          <v-btn
            text
            plain
            color="#545454"
            @mouseover="showMenu(3)"
          >
            Problemas que Atendemos
            <v-icon color="warning">
              mdi-menu-down
            </v-icon>
          </v-btn>

          <v-btn
            text
            plain
            color="#545454"
            @mouseover="showMenu(4)"
          >
            Charlas y Talleres
            <v-icon color="warning">
              mdi-menu-down
            </v-icon>
          </v-btn>
        </div>
      </div>
    </v-card>
    <fade-transition>
      <div
        v-if="
          menuEspecialidades || menuServicios || menuProblemas || menuCharlas
        "
        @mouseleave="hideMenu(1)"
      >
        <!-- MENUS -->
        <menu-especialidades
          v-if="menuEspecialidades"
          :split-especialidades-menu="splitEspecialidadesMenu"
        />
        <menu-servicios
          v-if="menuServicios"
          :split-servicios-menu="splitServiciosMenu"
        />
        <menu-problemas
          v-if="menuProblemas"
          :split-problemas-tipos-menu="splitProblemasTiposMenu"
        />
        <menu-charlas
          v-if="menuCharlas"
          :split-charlas-menu="splitCharlasMenu"
        />
        <!-- MENUS -->
      </div>
    </fade-transition>
  </v-container>
</template>

<script>
  import { mapMutations, mapState, mapGetters } from 'vuex'
  import { FadeTransition } from 'vue2-transitions'

  export default {
    name: 'SubAppBar',
    components: {
      FadeTransition,
      MenuProblemas: () => import('@/components/web/base/menu/MenuProblemas'),
      MenuServicios: () => import('@/components/web/base/menu/MenuServicios'),
      menuCharlas: () => import('@/components/web/base/menu/MenuCharlas'),
      menuEspecialidades: () =>
      import('@/components/web/base/menu/MenuEspecialidades'),
    },
    computed: {
      ...mapState('web', [
        'sucursales',
        'menuEspecialidades',
        'menuServicios',
        'menuCharlas',
        'menuProblemas',
      ]),
      ...mapGetters('web', [
        'splitEspecialidadesMenu',
        'splitServiciosMenu',
        'splitProblemasTiposMenu',
        'splitCharlasMenu',
      ]),
    },
    // created () {
    //   this.lala()
    // },
    methods: {
      ...mapMutations('web', ['SET_MENU']),

      showMenu (item) {
        this.SET_MENU({ item, value: true })
      },

      hideMenu (item) {
        this.SET_MENU({ item, value: false })
      },
      lala () {
        console.log('lala')
        // console.log(this.especialidadesHome)
        // console.log(this.splitEspecialidadesHome)
        // console.log(this.problemasTipos)
        console.log(this.splitEspecialidadesMenu)
      },
      returnToHome () {
        if (this.$route.name !== 'Home') this.$router.push({ path: '/' })
      },
    },
  }
</script>
